@keyframes gradient {
    0% {
        background-position: 0 0
    }

    100% {
        background-position: 100% 0
    }
}

.webflow-style-input {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 100%;
    margin: 0 auto;
    border-radius: 2px;
    padding: 1.4rem 2rem 1.6rem;
    background: rgba(57, 63, 84, 0.8);
}

.webflow-style-input input,
.webflow-style-input input:focus,
.webflow-style-input textarea,
.webflow-style-input textarea:focus {
    flex-grow: 1;
    color: #BFD2FF;
    font-size: 1.8rem;
    line-height: 2.4rem;
    vertical-align: middle;
    background: transparent;
    border: none;
    outline: none;
}

.webflow-style-input input::placeholder,
.webflow-style-input textarea::placeholder {
    color: #BFD2FF;
    opacity: 1;
}

.webflow-style-input input:-ms-input-placeholder,
.webflow-style-input textarea:-ms-input-placeholder {
    color: #BFD2FF;
}

.webflow-style-input input::-ms-input-placeholder,
.webflow-style-input textarea::-ms-input-placeholder {
    color: #BFD2FF;
}

.webflow-style-input button {
    font-size: 2.4rem;
    line-height: 2.4rem;
    vertical-align: middle;
    transition: color 0.25s;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
}

.webflow-style-input button svg {
    fill: #7881A1;
    min-width: 25px;
}

.webflow-style-input button:hover svg {
    color: #BFD2FF;
}

.webflow-style-input:after {
    content: "";
    position: absolute;
    left: 0px;
    right: 0px;
    bottom: 0px;
    z-index: 999;
    height: 4px;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
    background-position: 0% 0%;
    background: linear-gradient(to right, #B294FF, #57E6E6, #FEFFB8, #57E6E6, #B294FF, #57E6E6);
    background-size: 500% auto;
    -webkit-animation: gradient 3s linear infinite;
    animation: gradient 3s linear infinite;
}

